section.society-rule-container {
  width: clamp(28rem, 100%, 80.6rem);
}

article.society-rule-article {
  width: clamp(20rem, 98%, 79.1rem);
  margin: 0 auto;
}

h2.society-rule-header {
  font-size: clamp(1.7rem, 1.7vw, 2.3rem);
  font-weight: var(--small-fw);
  line-height: clamp(2.72, 2vw, 2.84rem);
}

ul.society-rule-list {
  display: grid;
  gap: 2rem;
  margin-top: 1.3rem;
}

li.society-rule-item {
  font-size: clamp(var(--m-small-text-size), 1.4vw, 1.8rem);
  font-weight: var(--ex-small-fw);
  line-height: clamp(2.6, 2.6vw, 2.5rem);
  padding: 1rem;
}

.society-rule-article div {
  margin: 0 auto;
  margin-top: 5.1rem;
  width: clamp(14.4rem, 43.7%, 79.6rem);
}

.society-rule-article button {
  width: 100%;
  height: clamp(4.5rem, 4.5vw, 5.5rem);
  background-color: var(--light-blue);
  color: var(--primary-white-color);
  margin-bottom: 12rem;
  border-radius: 1.2rem;
}
