section.society-container {
  width: clamp(28rem, 100%, 112rem);
}

article.societies-list-wrapper {
  width: clamp(20rem, 96.4%, 108rem);
  /* width: 100%; */
  margin: 0 auto;
  margin-left: 1rem;
  /* border: 0.1rem solid black; */
}

.societies-list-wrapper h2 {
  font-size: clamp(var(--m-tertiary-font), 1.8vw, 2.8rem);
  font-weight: var(--small-fw);
  line-height: clamp(2.194rem, 2vw, 3.413rem);
  margin-top: 4.4rem;
  margin-bottom: 0.7rem;
}

ul.societies-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 1.7rem;
}

li.societies-list-item {
  width: clamp(10rem, 10vw, 33.7rem);
  /* margin: 0 auto; */
  width: 100%;
  cursor: pointer;
}

div.societies-list-image img {
  width: 100%;
  height: 100%;
}

div.societies-list-text {
  text-align: center;
}

div.societies-list-text h3 {
  font-size: clamp(var(--m-secondary-font), 1.6vw, 3rem);
  font-weight: var(--small-fw);
  line-height: clamp(var(--m-ex-small-line-height), 1.9vw, 3.657rem);
}

div.societies-list-text small {
  font-size: clamp(1.2rem, 1.2vw, 2rem);
  font-weight: var(--ex-small-fw);
  line-height: clamp(1.463rem, 1.2vw, 2.438rem);
}

@media (min-width: 500px) {
  article.societies-list-wrapper {
    margin-left: 2rem;
  }

  ul.societies-list {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media (min-width: 768px) {
  article.societies-list-wrapper {
    margin-left: 3rem;
  }

  ul.societies-list {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
